import type { ComponentPropsWithoutRef, ElementType } from "react";
import { cn } from "@/lib/utils";

type ButtonVariant = "primary" | "secondary" | "ghost";

type Props = ComponentPropsWithoutRef<"button"> & {
  variant?: ButtonVariant;
  leftIcon?: ElementType;
};

export function Button({
  className,
  variant = "primary",
  leftIcon: LeftIcon,
  disabled,
  children,
  ...props
}: Props) {
  return (
    <button
      className={cn(
        "inline-flex h-9 items-center justify-center gap-2 rounded-lg px-3 text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50",
        variant === "primary" && "bg-primary text-primary-foreground hover:bg-primary/90",
        variant === "secondary" && "bg-secondary text-secondary-foreground hover:bg-secondary/80",
        variant === "ghost" && "hover:bg-accent hover:text-accent-foreground text-foreground",
        className
      )}
      disabled={disabled}
      {...props}
    >
      {LeftIcon ? <LeftIcon className="size-4" /> : null}
      {children}
    </button>
  );
}

