import { cn } from "@/lib/utils";
import type { WizardStep } from "@/core/catalog";

const steps: { id: WizardStep; label: string }[] = [
  { id: 1, label: "Contexto" },
  { id: 2, label: "Tcnicas" },
  { id: 3, label: "Configurao" },
  { id: 4, label: "Reviso & Export" },
];

export function Stepper({ step }: { step: WizardStep }) {
  return (
    <div className="rounded-xl bg-muted/30 p-3 ring-1 ring-border">
      <div className="grid grid-cols-4 gap-2">
        {steps.map((s) => {
          const state = s.id === step ? "active" : s.id < step ? "done" : "todo";
          return (
            <div
              key={s.id}
              className={cn(
                "flex items-center gap-2 rounded-lg px-2 py-2 transition-colors",
                state === "active" && "bg-primary/10 ring-1 ring-primary/30",
                state === "done" && "bg-secondary/50 ring-1 ring-border",
                state === "todo" && "bg-muted/50 ring-1 ring-border"
              )}
            >
              <div
                className={cn(
                  "grid size-6 place-items-center rounded-md text-xs font-semibold transition-colors",
                  state === "active" && "bg-primary text-primary-foreground",
                  state === "done" && "bg-secondary-foreground/10 text-secondary-foreground",
                  state === "todo" && "bg-muted text-muted-foreground"
                )}
              >
                {s.id}
              </div>
              <div className={cn("text-xs font-medium", state === "todo" ? "text-muted-foreground" : "text-foreground")}>{s.label}</div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
