import { Dialog } from "@/components/ui/Dialog";
import { Badge } from "@/components/ui/Badge";
import { TECHNIQUES } from "@/core/catalog";

type Props = {
  open: boolean;
  onClose: () => void;
};

export function HowItWorksDialog({ open, onClose }: Props) {
  return (
    <Dialog
      open={open}
      title="Como funciona"
      description="Cada tcnica gera casos de teste de forma independente. Depois, o sistema combina os resultados automaticamente, removendo duplicidades."
      onClose={onClose}
    >
      <div className="space-y-4">
        <div className="rounded-xl bg-zinc-900/60 p-3 text-sm text-zinc-200 ring-1 ring-zinc-800">
          <div className="font-semibold">Regra de ouro</div>
          <ul className="mt-2 space-y-1 text-xs text-zinc-300 list-none">
            <li> <strong className="text-zinc-100">O que testar</strong>  Particionamento de Equivalncia</li>
            <li> <strong className="text-zinc-100">Quais valores testar</strong>  Anlise de Valor Limite (BVA)</li>
            <li> <strong className="text-zinc-100">Quando cada resultado ocorre</strong>  Tabela de Deciso</li>
            <li> <strong className="text-zinc-100">Onde pode dar errado</strong>  Error Guessing</li>
          </ul>
        </div>
        <div className="grid gap-2">
          {TECHNIQUES.map((t) => (
            <div key={t.id} className="rounded-xl bg-zinc-950 p-3 ring-1 ring-zinc-800">
              <div className="flex items-center justify-between gap-3">
                <div className="text-sm font-semibold">{t.label}</div>
                <Badge>{t.id}</Badge>
              </div>
              <div className="mt-1 text-xs text-zinc-400">{t.blurb}</div>
            </div>
          ))}
        </div>
      </div>
    </Dialog>
  );
}
